var _self,options;
/* import {
    mapMutations  
} from 'vuex'; */
export default {
	data() {
		return {
			did:"",
			mobile:"",
			//手机验证码
			validateCode:""
		}
	},
	methods: {
		toLogin:function(){
			
			//判断经销商编号、手机号和验证码是否正确的格式，错误不提交
			if(_self.validateCode.length != 6 ){uni.showToast({title:"请输入正确的验证码",icon:"none"});return;}
			if(_self.did.length < 1){uni.showToast({title:"请输入正确的经销商编号",icon:"none"}); return;}
			if(_self.mobile.length != 11){uni.showToast({title:"请输入正确的手机号码",icon:"none"}); return;}
			
			uni.request({
				url: _self.apiServer+'app/distributor/login',
				method: 'POST',
				header: {'content-type' : "application/x-www-form-urlencoded"},
				data: {
					did : _self.did,
					mobile : _self.mobile,
					validateCode : _self.validateCode
				},
				success: res => {
					console.log(res);
					console.log(res.data.status);
					//从后台传过来一个json对象  获取其状态	
					if(res.data.status == 200){
						
						uni.showToast({title:"登录成功"});
						uni.setStorageSync('DID',res.data.data.did);
						
						if(_self.options.backtype == ''){
							uni.switchTab({
								url:"../product/product"
							})
						}
						
						// 跳转
						if(_self.options.backtype == 1){
							uni.redirectTo({url:_self.options.backpage});
						}else{
							uni.switchTab({url:_self.options.backpage});
						}
					}
					else if(res.data.status == 1301){
						uni.showToast({
							title:"验证码错误，请输入正确的验证码",
							icon:"none"
						})
					}
					else{
						//当失败时显示失败信息
						uni.showToast({title:res.data.msg});
					}
				},
				fail: (e) => {
					console.log(e);
					uni.showToast({
						title:"验证码获得失败，请重新尝试!",
						icon:"none"
					})
				},
				complete: () => {}
			});
			
		},
		//发送请求从后台获得验证码
		getValidate:function(){
			//_self.validateCode = '123456';
			if(_self.mobile.length!=11){
				uni.showToast({
					title:"请输入正确的手机号码",
					icon:"none"
				})
				return;
			}
			
			uni.request({
				url: _self.apiServer+'app/distributor/getValidateCode',
				method: 'GET',
				data: {
					mobile:_self.mobile
				},
				success: res => {
					//console.log(res.data.data);
					//从后台传过来一个json对象  获取其状态
					if(res.data.status == 200){
						uni.showToast({
							title:"验证码有效期为5分钟",
							icon:"success"
							
						})
						//进行验证码赋值  当有条件的情况下 可以用短信取代
						_self.validateCode = res.data.data;
						
					}else{
						
						uni.showToast({
							title:res.data.msg,
							icon:"none"
						})
					}
				},
				fail: (e) => {},
				complete: () => {}
			});
		}
	},
	onLoad:function(options){
		_self = this;
		//console.log(options);
		this.options = options;
				
	}
}
